import React from "react";
import { Box, Typography, Grid, Card, Container } from "@mui/material";
import Carousel from "nuka-carousel";
import zombieChef from "../assets/CryptoChefs/zombie.svg";
import hipsterChef from "../assets/CryptoChefs/hipster.svg";
import ninjaChef from "../assets/CryptoChefs/ninja.svg";
import cyborgChef from "../assets/CryptoChefs/cyborg.svg";
import vampireChef from "../assets/CryptoChefs/vampire.svg";
import back from "../assets/Arrows/back-arrow.svg";
import forward from "../assets/Arrows/forward-arrow.svg";
import { ElipseTop, ElipseBottom } from "../utils/theme";
import { NextButton, BackButton } from "../utils/theme.js";

const MeetCryptoChefsComponent = () => {
  const chefs = [
    {
      name: "Zombie CHEF",
      image: zombieChef,
    },
    {
      name: "Hipster CHEF",
      image: hipsterChef,
    },
    {
      name: "Ninja CHEF",
      image: ninjaChef,
    },
    {
      name: "Cyborg CHEF",
      image: cyborgChef,
    },
    {
      name: "Vampire CHEF",
      image: vampireChef,
    },
  ];

  return (
    <Box sx={{ backgroundColor: "secondary.main" }}>
      <ElipseTop></ElipseTop>
      <Grid container flexDirection="column" alignItems="center">
        <Typography variant="h2">MEET THE CHEFS!</Typography>
        <Container sx={{ width: "100%", height: "90%" }}>
          <Carousel
            autoplayInterval={10000}
            transitionMode="fade"
            wrapAround
            autoplay
            renderCenterLeftControls={({ previousSlide }) => (
              <BackButton onClick={previousSlide}>
                <img src={back} alt="back-button" />
              </BackButton>
            )}
            renderCenterRightControls={({ nextSlide }) => (
              <NextButton onClick={nextSlide}>
                <img src={forward} alt="next-button" />
              </NextButton>
            )}
            defaultControlsConfig={{
              pagingDotsStyle: {
                fill: "transparent",
              },
            }}
          >
            {chefs.map((chef, index, array) => (
              <Grid
                container
                item
                md={12}
                justifyContent="center"
                alignItems="center"
                key={`chef:${chef.name}`}
              >
                {index === 0 ? (
                  <Card>
                    <img
                      src={array[array.length - 1].image}
                      alt={array[array.length - 1].name}
                      width="100%"
                      height="auto"
                    ></img>
                  </Card>
                ) : (
                  <Card>
                    <img
                      src={array[index - 1].image}
                      alt={array[index - 1].name}
                      width="100%"
                      height="auto"
                    ></img>
                  </Card>
                )}
                <Box
                  width="300px"
                  height="600px"
                  sx={{
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                    justifyContent: "space-between",
                    "@media (max-width:900px)": {
                      width: "180px",
                      height: "375px",
                    },
                  }}
                >
                  <img src={chef.image} width="100%" alt={chef.name}></img>
                  <Typography variant="h3" textAlign="center" mt="-2vw">
                    {chef.name}
                  </Typography>
                </Box>
                {index === array.length - 1 ? (
                  <Card>
                    <img
                      src={array[0].image}
                      alt={array[0].name}
                      width="100%"
                      height="auto"
                    ></img>
                  </Card>
                ) : (
                  <Card>
                    <img
                      src={array[index + 1].image}
                      alt={array[index + 1].name}
                      width="100%"
                      height="auto"
                    ></img>
                  </Card>
                )}
              </Grid>
            ))}
          </Carousel>
        </Container>
      </Grid>
      <ElipseBottom></ElipseBottom>
    </Box>
  );
};

export default MeetCryptoChefsComponent;
